import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
const _import = file => () => import(`@/views/${file}`)

const routerMap = [
  {
    path: '/not-wechat',
    name: 'NotWechat',
    component: _import('error/not-wechat'),
    meta: {
      title: '非微信环境'
    }
  },
  {
    path: '/',
    name: 'Index',
    component: _import('index/index'),
    meta: {
      title: '首页'
    }
  },
  {
    path: '/index/components/antiFake',
    name: 'AntiFake',
    component: _import('index/components/antiFake'),
    meta: {
      title: '防伪查询'
    }
  },
  {
    path: '/index/components/aboutUs',
    name: 'AboutUs',
    component: _import('index/components/aboutUs'),
    meta: {
      title: '关于我们'
    }
  },
  {
    path: '/index/components/service',
    name: 'Service',
    component: _import('index/components/service'),
    meta: {
      title: '客服留言'
    }
  },
  {
    path: '/healthCenter',
    name: 'InHealthCenterdex',
    component: _import('healthCenter/index'),
    meta: {
      title: '健康中心'
    }
  },
  {
    path: '/healthCenter',
    name: 'HealthCenter',
    component: _import('healthCenter/index'),
    children: [
      {
        path: 'components/solution',
        name: 'Solution',
        component: _import('components/solution'),
        meta: {
          title: '解决问题'
        }
      },
      {
        path: 'components/classificationNav',
        name: 'ClassificationNav',
        component: _import('components/classificationNav'),
        meta: {
          title: '分类导航'
        }
      }
    ]
  },
  {
    path: '/healthCenter/components/articleDetails/:id',
    name: 'ArticleDetails',
    component: _import('healthCenter/components/articleDetails'),
    meta: {
      title: '文章详情'
    }
  },
  {
    path: '/healthCenter/components/classificationDetails/:id',
    name: 'ClassificationDetails',
    component: _import('healthCenter/components/classificationDetails'),
    meta: {
      title: '分类详情'
    }
  },
  {
    path: '/giftCenter',
    name: 'GiftCenter',
    component: _import('giftCenter/index'),
    meta: {
      title: '礼品中心'
    }
  },
  {
    path: '/memberCenter',
    name: 'MemberCenter',
    component: _import('memberCenter/index'),
    meta: {
      title: '会员中心'
    }
  },
  {
    path: '/memberCenter/user',
    name: 'User',
    component: _import('memberCenter/user'),
    meta: {
      title: '登录'
    }
  },
  {
    path: '/memberCenter/components/addressManagement',
    name: 'AddressManagement',
    component: _import('memberCenter/components/addressManagement'),
    meta: {
      title: '收货地址管理'
    }
  },
  {
    path: '/memberCenter/components/addressEdit/:id',
    name: 'AddressEdit',
    component: _import('memberCenter/components/addressEdit'),
    meta: {
      title: '收货地址编辑'
    }
  },
  {
    path: '/memberCenter/components/addAddress',
    name: 'AddAddress',
    component: _import('memberCenter/components/addAddress'),
    meta: {
      title: '添加收货地址'
    }
  },
  {
    path: '/memberCenter/components/forgetPassword',
    name: 'ForgetPassword',
    component: _import('memberCenter/components/forgetPassword'),
    meta: {
      title: '忘记密码'
    }
  },
  {
    path: '/memberCenter/components/prizeList',
    name: 'PrizeList',
    component: _import('memberCenter/components/prizeList'),
    meta: {
      title: '已领取奖品'
    }
  },
  {
    path: '/memberCenter/components/forgetPassword',
    name: 'ForgetPassword',
    component: _import('memberCenter/components/forgetPassword'),
    meta: {
      title: '忘记密码'
    }
  },
  {
    path: '/memberCenter/components/register',
    name: 'Register',
    component: _import('memberCenter/components/register'),
    meta: {
      title: '注册'
    }
  }
]
const routerInstance = new Router({
  mode: 'history',
  routes: routerMap
})
routerInstance.beforeEach((to, from, next) => {
  if (to.path === '/memberCenter') {
    next()
  } else {
    let token = localStorage.getItem('AccessToken')

    if (token === 'null' || token === '') {
      next('/memberCenter/user')
    } else {
      next()
    }
  }
})
export default routerInstance



